.certificates-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #2c3e50 0%, #34495e 50%, #4a5568 100%);
  padding: 2rem;
  font-family: 'Georgia', 'Times New Roman', serif;
  position: relative;
  overflow: hidden;
}



.floating-certificate-elements {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.achievement-badge {
  position: absolute;
  color: rgba(255, 255, 255, 0.2);
  font-size: 0.9rem;
  font-weight: 700;
  animation: badgeFloat 15s ease-in-out infinite;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.achievement-badge:nth-child(1) {
  top: 18%;
  left: 6%;
  animation-delay: 0s;
}

.achievement-badge:nth-child(2) {
  top: 35%;
  right: 10%;
  animation-delay: 4s;
}

.achievement-badge:nth-child(3) {
  bottom: 28%;
  left: 8%;
  animation-delay: 8s;
}

@keyframes badgeFloat {
  0%, 100% { transform: translateY(0) rotate(-2deg); opacity: 0.2; }
  50% { transform: translateY(-20px) rotate(2deg); opacity: 0.3; }
}

.certificates-header {
  text-align: center;
  margin-bottom: 4rem;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(15px);
  padding: 3rem;
  border-radius: 30px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.3);
  max-width: 800px;
  margin: 0 auto 4rem auto;
  position: relative;
  z-index: 2;
}

.page-title {
  font-size: 3.5rem;
  color: #2c3e50;
  margin: 0 0 1rem 0;
  font-weight: 300;
  letter-spacing: 3px;
}

.page-subtitle {
  font-size: 1.4rem;
  color: #7f8c8d;
  margin: 0;
  font-style: italic;
}

.certificates-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.certificate-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(15px);
  border-radius: 16px;
  padding: 0 !important;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  position: relative;
  overflow: visible;
  display: flex;
  flex-direction: column;
  height: 100%;
  opacity: 1;
}

.certificate-card .p-card-body {
  padding: 1.5rem !important;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.certificate-card .p-card-content {
  padding: 0 !important;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.certificate-card .p-card-footer {
  padding: 1rem 1.5rem 1.5rem 1.5rem !important;
  margin-top: auto;
}

/* Ensure PrimeNG components are visible */
p-card, p-tag, p-badge, p-image {
  display: block !important;
  visibility: visible !important;
  opacity: 1 !important;
}

/* Consistent image sizes in certificate cards */
.certificates-grid .p-card .p-card-header {
  padding: 0 !important;
  margin: 0 !important;
}

.certificates-grid .p-image {
  width: 100% !important;
  height: 180px !important;
  overflow: hidden !important;
  border-radius: 12px 12px 0 0 !important;
  margin: 0 0 1rem 0 !important;
}

.certificates-grid .p-image img {
  width: 100% !important;
  height: 180px !important;
  object-fit: cover !important;
  border-radius: 0 !important;
  margin: 0 !important;
  transition: transform 0.3s ease !important;
}

.certificate-card:hover .p-image img {
  transform: scale(1.05) !important;
}

.certificate-card:nth-child(1) { animation: fadeIn 0.6s ease-out forwards; animation-delay: 0.1s; }
.certificate-card:nth-child(2) { animation: fadeIn 0.6s ease-out forwards; animation-delay: 0.3s; }
.certificate-card:nth-child(3) { animation: fadeIn 0.6s ease-out forwards; animation-delay: 0.5s; }
.certificate-card:nth-child(4) { animation: fadeIn 0.6s ease-out forwards; animation-delay: 0.7s; }

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.certificate-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  border-radius: 16px 16px 0 0;
  transform: scaleX(1);
  transition: transform 0.4s ease;
}

.certificate-card:nth-child(1)::before {
  background: linear-gradient(135deg, #27ae60, #2ecc71);
}

.certificate-card:nth-child(2)::before {
  background: linear-gradient(135deg, #e74c3c, #c0392b);
}

.certificate-card:nth-child(3)::before {
  background: linear-gradient(135deg, #f39c12, #e67e22);
}

.certificate-card:nth-child(4)::before {
  background: linear-gradient(135deg, #f1c40f, #f39c12);
}

.certificate-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 25px 50px rgba(74, 85, 104, 0.2);
}

.cert-image {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 12px;
  margin: 0 auto 1.5rem auto;
  display: block;
  transition: transform 0.3s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.certificate-card:hover .cert-image {
  transform: scale(1.02);
}

.cert-icon {
  font-size: 1rem;
  font-weight: 700;
  color: #667eea;
  background: rgba(102, 126, 234, 0.1);
  padding: 0.8rem 1.2rem;
  border-radius: 10px;
  margin: 0 auto 1.5rem auto;
  letter-spacing: 1px;
  border: 2px solid rgba(102, 126, 234, 0.2);
  display: inline-block;
  text-align: center;
  width: fit-content;
}

.certificate-card h3 {
  color: #2c3e50;
  font-size: 1.8rem;
  margin: 0 0 0.5rem 0;
  font-weight: 400;
  letter-spacing: 1px;
  text-align: center;
}

.cert-issuer {
  color: #667eea;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  text-align: left;
}

.certificate-card p {
  color: #34495e;
  line-height: 1.6;
  font-size: 0.95rem;
  margin: 0 0 1rem 0;
  text-align: left;
  flex-grow: 1;
}

.cert-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  justify-content: flex-start;
  margin-top: auto;
}

.skill-tag {
  background: linear-gradient(135deg, #4a5568, #2d3748);
  color: white;
  padding: 0.4rem 1rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
  box-shadow: 0 4px 10px rgba(74, 85, 104, 0.3);
  transition: transform 0.2s ease;
}

.skill-tag:hover {
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .certificates-container {
    padding: 1rem;
  }
  
  .certificates-header {
    padding: 2rem;
    margin-bottom: 3rem;
  }
  
  .page-title {
    font-size: 2.8rem;
    letter-spacing: 2px;
  }
  
  .certificates-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1.5rem;
    width: 100%;
    height: auto;
  }
  
  .certificates-grid .p-image {
    height: 160px !important;
  }
  
  .certificates-grid .p-image img {
    height: 160px !important;
  }
  
  .certificate-card .p-card-body {
    padding: 1.2rem !important;
  }
  
  .certificate-card .p-card-footer {
    padding: 1rem !important;
  }
  
  .certificate-card h3 {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .certificates-header {
    padding: 1.5rem;
  }
  
  .page-title {
    font-size: 2.2rem;
    letter-spacing: 1px;
  }
  
  .certificates-grid .p-image {
    height: 140px !important;
  }
  
  .certificates-grid .p-image img {
    height: 140px !important;
  }
  
  .certificate-card .p-card-body {
    padding: 1rem !important;
  }
  
  .certificate-card .p-card-footer {
    padding: 0.8rem !important;
  }
  
  .cert-skills {
    gap: 0.4rem;
  }
}